.home {
    font-family: "Quicksand", sans-serif;
    background-color: #eeecec;

    .container {
        .wrapper {
            .row {
                .hero {
                    display: flex;

                    //border: 1px solid #000;
                    .right {
                        .content {
                            p {
                                font-size: 100px;

                            }

                            img {
                                margin-left: 50px;
                            }
                        }
                    }
                }

            }
        }
    }
}

.services {
    width: 100%;
    height: 100%;

    //border: 2px solid #b31616;
    //overflow: scroll;
    .container {
        .wrapper {
            height: 100%;

            .title {
                margin-top: 50px;

                text-align: center;
            }

            .s-card {
                display: flex;
                flex-wrap: wrap;
                //border: 1px solid #000;

                .card {
                    margin: 20px 0px 10px 20px;
                    height: 480px;

                    #img {
                        width: 250px;
                        height: 250px;
                        padding: 10px 15px;
                        margin-left: 20px;
                    }

                    #text {
                        height: 100px;
                        margin-bottom: 25px;
                    }
                }
            }

        }
    }
}

.ourclient {
    margin-bottom: 50px;
    background-color: #edf2f4;
    margin-top: 50px;
    padding: 15px 10px;

    .container {
        .wrapper {
            .row {
                h1 {
                    text-align: center;
                }

                .card-body {
                    .container {
                        max-width: 1400px;
                        padding: 0px 15px;
                        margin: 0 auto;

                        .cards {
                            display: grid;
                            //grid-auto-columns: 100%;
                            grid-column-gap: 10px;
                            grid-auto-flow: column;
                            padding: 25px 0px;
                            list-style: none;
                            overflow-x: scroll;
                            -ms-scroll-snap-type: x mandatory;
                            scroll-snap-type: x mandatory;

                            .card {
                                display: flex;
                                flex-direction: column;
                                padding: 20px;
                                background: #fff;
                                scroll-snap-align: start;
                                transition: all 0.2s;
                                width: 300px;

                                .card-content {
                                    width: 200px;
                                    margin: 20px 0;
                                    max-width: 85%;

                                    img {
                                        margin-left: 25px;
                                        margin-right: 25px;

                                    }
                                }
                            }

                            .card:hover {
                                color: #fff;
                                background: rgb(194, 193, 193);
                            }


                        }

                        .cards::-webkit-scrollbar {
                            height: 12px;
                        }

                        .cards::-webkit-scrollbar-thumb,
                        .cards::-webkit-scrollbar-track {
                            border-radius: 92px;

                        }

                        .cards::-webkit-scrollbar-thumb {
                            background: rgb(194, 193, 193);
                        }

                        .cards::-webkit-scrollbar-track {
                            background: #edf2f4;
                        }

                    }
                }
            }
        }
    }
}

@media(max-width:991px) {
    .home {
        font-family: "Quicksand", sans-serif;
        background-color: #eeecec;
        width: 100%;
        height: 85vh;


        .container {
            .wrapper {
                .row {
                    .hero {
                        display: block;

                        .left {
                            .image {
                                width: 300px;
                                margin-left: 20px;

                                img {
                                    width: 100%;

                                }
                            }
                        }

                        .right {
                            margin-top: 0px;

                            .content {
                                margin-bottom: 200px;

                                p {
                                    font-size: 40px;
                                    margin-left: 20px;
                                }

                                img {
                                    margin-left: 20px;
                                    width: 200px;
                                }
                            }
                        }
                    }

                }
            }
        }
    }
}